import React, { Component, Fragment } from 'react';
import { withRouter } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import { Card, Row, Col, Table, Modal, message } from 'antd';
import { BtnAddNew } from 'components/common/button';
import { CartHeadAction, ColEditDelete } from 'components/common';
import {
  getSubjectGroupById,
  deleteSubjectGroup,
  getListStaff,
  createSubjectGroup,
} from 'services/StaffSubjectGroup';
import { getQueryParamId } from 'utils/urlUtil';

class FormSubjectGroupPage extends Component {
  state = {
    loading: false,
    visible: false,
  };

  async componentDidMount() {
    await this.getSubjectGroup(getQueryParamId());
    this.getListAddStaff();
  }

  componentWillReceiveProps = () => {};

  getListAddStaff = async () => {
    const listStaff = await getListStaff();
    this.setState({
      listStaff: listStaff.data.items,
    });
  };

  getSubjectGroup = async id => {
    const listSubject = await getSubjectGroupById(id);
    const dataTableStaff = {};
    listSubject.data.staffs.forEach(item => {
      dataTableStaff[item.staffId] = item;
    });
    this.setState({
      dataTable: listSubject.data,
      dataTableStaff,
    });
  };

  listAddStaff = async item => {
    const { dataTable } = this.state;
    const dataListAdd = { ...this.state.dataListAdd };
    const newStaffGroup = {
      subjectGroupId: dataTable.subjectGroupId,
      staffId: item.staffId,
    };
    await createSubjectGroup(newStaffGroup);
    await delete dataListAdd[item.staffId];
    // this.handleChangeQueryParams();
    await this.getSubjectGroup(getQueryParamId());
    this.setState({
      dataListAdd,
      visible: false,
    });
  };

  handleData = async () => {
    const { dataTable, listStaff } = this.state;
    const dataStaff = {};
    listStaff.forEach(item => {
      dataStaff[item.staffId] = item;
    });
    dataTable.staffs.forEach(item => {
      delete dataStaff[item.staffId];
    });
    this.setState({
      dataListAdd: dataStaff,
      visible: true,
    });
  };

  handleDelete = async id => {
    const { dataTable } = this.state;
    this.setState({
      loading: true,
    });
    const subjectDelete = {
      staffId: id,
      subjectGroupId: dataTable.subjectGroupId,
    };
    await deleteSubjectGroup(subjectDelete);
    message.success('Xóa thành công');
    this.setState({
      loading: false,
    });
    this.getSubjectGroup(getQueryParamId());
  };
  handleCancel = () => {
    this.setState({
      visible: false,
    });
  };

  render() {
    const { dataListAdd, dataTableStaff, dataTable } = this.state;
    const columns = [
      {
        title: 'Mã cán bộ',
        dataIndex: 'staffId',
        key: 'staffId',
      },
      {
        title: 'Tên cán bộ',
        dataIndex: 'fullname',
        key: 'fullname',
      },
      {
        title: 'Giới tính',
        dataIndex: 'genderText',
        key: 'genderText',
      },
      {
        title: 'Ngày sinh ',
        dataIndex: 'birthdate',
        key: 'birthdate',
      },
      {
        title: 'Chức danh',
        dataIndex: 'title',
        key: 'title',
      },
      {
        title: 'Chức vụ',
        dataIndex: 'position',
        key: 'position',
      },
      {
        title: 'Học hàm',
        dataIndex: 'rank',
        key: 'rank',
      },
      {
        title: 'Học vị',
        dataIndex: 'degree',
        key: 'degree',
      },
      {
        title: 'Thao tác',
        dataIndex: 'operation',
        render: (text, record) => (
          <ColEditDelete
            hidenBtnEdit="true"
            hrefEdit={`${this.urlFormEdit}/${record.subjectGroupId}`}
            onConfirmDelete={() => this.handleDelete(record.staffId)}
          />
        ),
      },
    ];
    const columnsAdd = [
      {
        title: 'Mã cán bộ',
        dataIndex: 'staffId',
        key: 'staffId',
      },
      {
        title: 'Tên cán bộ',
        dataIndex: 'fullname',
        key: 'fullname',
      },
      {
        title: 'Thao tác',
        dataIndex: 'operation',
        render: (text, record) => (
          <BtnAddNew title="Thêm" onClick={() => this.listAddStaff(record)} />
        ),
      },
    ];

    return (
      <Card title="">
        {dataTable ? (
          <Fragment>
            <Row style={{ marginBottom: 30 }}>
              <Col span={7}>
                <div style={{ fontSize: 16, fontWeight: 'bold' }}>
                  Mã bộ môn: {dataTable.subjectGroupId}{' '}
                </div>
              </Col>
              <Col span={7}>
                <div style={{ fontSize: 16, fontWeight: 'bold' }}>
                  {' '}
                  Tên bộ môn: {dataTable.label}{' '}
                </div>
              </Col>
              <Col span={7}>
                <div style={{ fontSize: 16, fontWeight: 'bold' }}>
                  SL giáo viên: {dataTable.staffs.length}{' '}
                </div>
              </Col>
            </Row>
            <Row>
              <CartHeadAction>
                <BtnAddNew onClick={this.handleData} />
              </CartHeadAction>
            </Row>

            <Row>
              <Col span={24}>
                <Table
                  loading={this.state.loading}
                  dataSource={Object.values(dataTableStaff)}
                  columns={columns}
                  rowKey="id"
                />
              </Col>
            </Row>
          </Fragment>
        ) : null}
        <Modal
          title="Thêm giảng viên"
          visible={this.state.visible}
          footer={null}
          onCancel={this.handleCancel}
        >
          {dataTable ? (
            <Table
              dataSource={dataListAdd ? Object.values(dataListAdd) : []}
              columns={columnsAdd}
              rowKey="name"
            />
          ) : null}
        </Modal>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <FormSubjectGroupPage {...props} />
  </Page>
));



