import { request } from 'utils';

const prefix = '/api/organization';

export const getListSubjectGroup = () =>
  request({
    prefix,
    url: '/Staff/SubjectGroup',
    params: {},
    method: 'GET',
  });
export const getSubjectGroupById = id =>
  request({
    prefix,
    url: `/Staff/SubjectGroup/GetById`,
    params: {
      subjectGroupId: id,
    },
    method: 'GET',
  });

export const getListStaff = () =>
  request({
    prefix,
    url: `/Staff`,
    params: {
      isPagingEnabled: false,
    },
    method: 'GET',
  });
export const deleteSubjectGroup = subject =>
  request({
    prefix,
    url: `/Staff/SubjectGroup`,
    params: { ...subject },
    method: 'DELETE',
  });
export const updateSubjectGroup = (id, values) =>
  request({
    prefix,
    url: `/SubjectGroup/${id}`,
    data: {
      id: values.id,
      label: values.code,
      btId: null,
    },
    method: 'PUT',
  });

export const createSubjectGroup = newStaff =>
  request({
    prefix,
    url: '/Staff/SubjectGroup',
    data: {
      ...newStaff,
    },
    method: 'POST',
  });



